import Vue from 'vue'
import { requestPayDetail } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import { InfiniteScroll, Spinner } from 'mint-ui'
Vue.use(InfiniteScroll)
Vue.component(Spinner.name, Spinner)

export default {
  name: 'financialDetails',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      page: 1,
      pageSize: 20,
      list: [],
      loading: false,
      isEnd: false
    }
  },
  mounted () {
    this.loadMore()
  },
  methods: {
    async getPayDetail () {
      let res = await requestPayDetail(this.$store.state.User.token, this.page, this.pageSize)
      if (res.code === 200) {
        this.page += 1
        let pageData = res.data.page_data
        if (pageData.length < this.pageSize) {
          this.isEnd = true
        }
        this.list = [...this.list, ...pageData]
      } else {
        window.EventBus.$emit('globalMessage', res.msg)
      }
      this.loading = false
    },
    loadMore () {
      if (this.isEnd) return
      this.loading = true
      this.getPayDetail()
    }
  }
}



